@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.ColorBox {
  width: 20%;
  height: 25%;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  cursor: pointer;
  margin-bottom: -5px;
}
.copy-button {
  width: 100px;
  height: 30px;
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  margin-left: -50px;
  margin-top: -15px;
  text-align: center;
  outline: none;
  background: rgba(255, 255, 255, 0.3);
  font-size: 1rem;
  line-height: 030px;
  color: white;
  text-transform: uppercase;
  border: none;
  opacity: 0;
  transition: 0.3s ease-in;
}
.ColorBox:hover .copy-button {
  opacity: 1;
}
.box-content {
  position: absolute;
  width: 100%;
  left: 0px;
  bottom: 0px;
  padding: 10px;
  color: black;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 12px;
}
.see-more {
  background: rgba(255, 255, 255, 0.3);
  position: absolute;
  border: none;
  right: 0px;
  bottom: 0px;
  color: white;
  width: 60px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  text-transform: uppercase;
}
.copy-overlay {
  opacity: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  transform: scale(0.1);
  transition: transform 0.6s ease-in-out;
}
.copy-overlay.show {
  opacity: 1;
  transform: scale(50);
  z-index: 10;
  position: absolute;
}
.copy-msg {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2em;
  transform: scale(0.1);
  opacity: 0;
  color: white;
}
.copy-msg.show {
  opacity: 1;
  transform: scale(1);
  z-index: 25;
  transition: all 0.4s ease-in-out;
  transition-delay: 0.3s;
}
.copy-msg h1 {
  font-weight: 400;
  text-shadow: 1px 1.5px black;
  background: rgba(255, 255, 255, 0.2);
  width: 100%;
  margin-bottom: 0;
  padding: 1rem;
  text-align: center;
  text-transform: uppercase;
}
.copy-msg p {
  font-size: 1rem;
  font-weight: 200;
}

.Palette {
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.Palette-colors {
  height: 90%;
}
.Palette-footer {
  background-color: white;
  height: 5vh;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-weight: bold;
}
.emoji {
  font-size: 1.5rem;
  margin: 0 1rem;
}

.NavBar {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 6vh;
}
.logo {
  margin-right: 15px;
  padding: 0 13px;
  font-size: 22px;
  background-color: #eceff1;
  font-family: Roboto;
  height: 100%;
  display: flex;
  align-items: center;
}
.logo a {
  text-decoration: none;
  color: black;
}
.slider {
  width: 340px;
  margin: 0 10px;
  display: inline-block;
}
.rc-slider-track {
  background-color: transparent;
}
.rc-slider-handle,
.rc-slider-handle:focus,
.rc-slider-handle:active,
.rc-slider-handle:hover {
  background-color: green;
  outline: none;
  border: 2px solid green;
  box-shadow: none;
  width: 13px;
  height: 13px;
  margin-left: -7px;
  margin-top: -3px;
}
.rc-slider-rail {
  height: 6px;
}
.select-container {
  margin-left: auto;
  margin-right: 1rem;
}

